'use client';

import { useState, useCallback, useRef } from 'react';
import { motion } from 'framer-motion';
import { FileText, Download, ArrowLeft } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { useRouter } from 'next/navigation';
import FileUploadPreview from './FileUploadPreview';
import AutoFillInstructions from './AutoFillInstructions';
import ManualFieldEditor from './ManualFieldEditor';

interface FormField {
  id: string;
  name: string;
  value: string | boolean | string[];
  type: 'text' | 'checkbox' | 'radio' | 'dropdown';
  options?: string[];
  x: number;
  y: number;
  width: number;
  height: number;
}

function App() {
  const [pdfFile, setPdfFile] = useState<File | null>(null);
  const [instructions, setInstructions] = useState('');
  const [formFields, setFormFields] = useState<FormField[]>([]);
  const [filledFields, setFilledFields] = useState<FormField[]>([]);
  const instructionsRef = useRef('');
  const router = useRouter();

  // Keep ref in sync with instructions state
  const setInstructionsWithRef = useCallback((newInstructions: string) => {
    setInstructions(newInstructions);
    instructionsRef.current = newInstructions;
  }, []);

  const updateFieldValue = useCallback((id: string, value: string | boolean | string[]) => {
    setFormFields(fields => fields.map(field => field.id === id ? { ...field, value } : field));
    // Only update preview when button is clicked, not on field changes
  }, []);

  const handlePdfLoad = useCallback((file: File) => {
    setPdfFile(file);
    // Reset filled fields when new PDF is loaded
    setFilledFields([]);
  }, []);

  const handleFieldsExtracted = useCallback((fields: FormField[]) => {
    setFormFields(fields);
  }, []);

  const autoFillWithInstructions = useCallback(() => {
    setFormFields(currentFields => {
      const currentInstructions = instructionsRef.current;
      const updatedFields = [...currentFields];
      
      if (currentInstructions.trim()) {
        const lines = currentInstructions.split('\n').filter(line => line.trim());
        
        lines.forEach(line => {
          const colonMatch = line.match(/^(.+?):\s*(.+)$/);
          const equalsMatch = line.match(/^(.+?)\s*=\s*(.+)$/);
          
          const match = colonMatch || equalsMatch;
          if (match) {
            const fieldName = match[1].trim();
            const fieldValue = match[2].trim();
            
            // Enhanced field matching
            const matchingFieldIndex = updatedFields.findIndex(field => {
              const normalizedFieldName = field.name.toLowerCase().replace(/[^\w\s]/g, ' ').replace(/\s+/g, ' ').trim();
              const normalizedInputName = fieldName.toLowerCase().replace(/[^\w\s]/g, ' ').replace(/\s+/g, ' ').trim();
              
              // Direct match
              if (normalizedFieldName === normalizedInputName) return true;
              
              // Contains match (either direction)
              if (normalizedFieldName.includes(normalizedInputName) || 
                  normalizedInputName.includes(normalizedFieldName)) return true;
              
              // Key word matching for common field types
              const fieldKeywords = ['named', 'insured', 'effective', 'date', 'address', 'phone', 'email', 'company', 'contact', 'primary', 'producer', 'agency', 'network'];
              
              const inputWords = normalizedInputName.split(/\s+/);
              const fieldWords = normalizedFieldName.split(/\s+/);
              
              // Check for keyword matches
              const hasKeywordMatch = inputWords.some(inputWord => 
                fieldKeywords.includes(inputWord) && fieldWords.some(fieldWord => 
                  fieldWord.includes(inputWord) || inputWord.includes(fieldWord)
                )
              );
              
              if (hasKeywordMatch) return true;
              
              // Fuzzy word matching
              const matchingWords = inputWords.filter(inputWord => 
                fieldWords.some(fieldWord => 
                  fieldWord.includes(inputWord) || inputWord.includes(fieldWord)
                )
              );
              
              return matchingWords.length >= Math.min(inputWords.length, fieldWords.length) * 0.5;
            });
            
            if (matchingFieldIndex !== -1) {
              const matchingField = updatedFields[matchingFieldIndex];
              let newValue: string | boolean | string[];
              
              if (matchingField.type === 'checkbox') {
                newValue = /^(true|yes|1|checked|on|x)$/i.test(fieldValue);
              } else if (matchingField.type === 'radio' || matchingField.type === 'dropdown') {
                const matchingOption = matchingField.options?.find(option =>
                  option.toLowerCase().includes(fieldValue.toLowerCase()) ||
                  fieldValue.toLowerCase().includes(option.toLowerCase())
                );
                newValue = matchingOption || fieldValue;
              } else {
                newValue = fieldValue;
              }
              
              updatedFields[matchingFieldIndex] = { ...matchingField, value: newValue };
              console.log(`Filled "${matchingField.name}" with "${fieldValue}"`);
            } else {
              console.warn(`Could not find field matching "${fieldName}"`);
            }
          }
        });
      }
      
      setFilledFields(updatedFields);
      return updatedFields;
    });
  }, []);

  const downloadPDF = useCallback(() => {
    const link = document.createElement('a');
    link.href = 'data:application/pdf;base64,JVBERi0xLjQKJdPr6eEKMSAwIG9iago8PAovVHlwZSAvQ2F0YWxvZwovUGFnZXMgMiAwIFIKPj4KZW5kb2JqCg==';
    link.download = 'filled-form.pdf';
    link.click();
  }, []);

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-50">
      <header className="bg-white shadow-sm border-b border-slate-200">
        <div className="max-w-7xl mx-auto px-6 py-4 flex justify-between items-center">
          <div className="flex items-center gap-3">
            <div className="p-2 bg-blue-100 rounded-lg">
              <FileText className="w-6 h-6 text-blue-600" />
            </div>
            <div>
              <h1 className="text-xl font-semibold text-slate-900">PDF Form Filler</h1>
              <p className="text-sm text-slate-600">Upload, edit, and download your PDF forms</p>
            </div>
          </div>
          <button
            onClick={downloadPDF}
            disabled={!pdfFile}
            className="flex items-center space-x-2 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:opacity-50"
          >
            <Download className="w-4 h-4" />
            <span>Download PDF</span>
          </button>
        </div>
      </header>

      <div className="max-w-7xl mx-auto px-6 py-8 grid grid-cols-1 xl:grid-cols-2 gap-8 min-h-[calc(100vh-200px)]">
        <FileUploadPreview 
          onPdfLoad={handlePdfLoad}
          onFieldsExtracted={handleFieldsExtracted}
          filledFields={filledFields}
        />

        <div className="space-y-6">
          <AutoFillInstructions
            instructions={instructions}
            setInstructions={setInstructionsWithRef}
            autoFill={autoFillWithInstructions}
            disabled={!pdfFile}
          />
          <ManualFieldEditor formFields={formFields} updateFieldValue={updateFieldValue} />
        </div>
      </div>
    </div>
  );
}

export default App;
