import { memo, useCallback } from 'react';
import { Edit3 } from 'lucide-react';

export interface FormField {
  id: string;
  name: string;
  value: string | boolean | string[];
  type: 'text' | 'checkbox' | 'radio' | 'dropdown';
  options?: string[];
  x: number;
  y: number;
  width: number;
  height: number;
}

interface Props {
  formFields: FormField[];
  updateFieldValue: (id: string, value: string | boolean | string[]) => void;
}

const ManualFieldEditor = memo(function ManualFieldEditor({ formFields, updateFieldValue }: Props) {
  const renderField = useCallback((field: FormField) => {
    switch (field.type) {
      case 'checkbox':
        return (
          <div key={field.id}>
            <label className="flex items-center space-x-2">
              <input
                type="checkbox"
                checked={field.value as boolean}
                onChange={(e) => updateFieldValue(field.id, e.target.checked)}
                className="rounded border-slate-300"
              />
              <span className="text-sm font-medium text-slate-700">{field.name}</span>
            </label>
          </div>
        );

      case 'radio':
        return (
          <div key={field.id}>
            <label className="block text-sm font-medium text-slate-700 mb-2">{field.name}</label>
            <div className="space-y-2">
              {field.options?.map((option) => (
                <label key={option} className="flex items-center space-x-2">
                  <input
                    type="radio"
                    name={field.id}
                    value={option}
                    checked={field.value === option}
                    onChange={(e) => updateFieldValue(field.id, e.target.value)}
                    className="border-slate-300"
                  />
                  <span className="text-sm text-slate-700">{option}</span>
                </label>
              ))}
            </div>
          </div>
        );

      case 'dropdown':
        return (
          <div key={field.id}>
            <label className="block text-sm font-medium text-slate-700 mb-1">{field.name}</label>
            <select
              value={Array.isArray(field.value) ? field.value[0] || '' : field.value as string}
              onChange={(e) => updateFieldValue(field.id, e.target.value)}
              className="w-full border border-slate-300 rounded px-3 py-2"
            >
              <option value="">Select an option</option>
              {field.options?.map((option) => (
                <option key={option} value={option}>{option}</option>
              ))}
            </select>
          </div>
        );

      default: // text
        return (
          <div key={field.id}>
            <label className="block text-sm font-medium text-slate-700 mb-1">{field.name}</label>
            <input
              type="text"
              value={field.value as string}
              onChange={(e) => updateFieldValue(field.id, e.target.value)}
              className="w-full border border-slate-300 rounded px-3 py-2"
              placeholder={`Enter ${field.name.toLowerCase()}`}
            />
          </div>
        );
    }
  }, [updateFieldValue]);

  return (
    <div className="bg-white rounded-xl shadow-lg border border-slate-200">
      <div className="p-4 bg-slate-50 border-b border-slate-200 flex items-center">
        <Edit3 className="w-5 h-5 text-slate-600 mr-2" />
        <h2 className="text-lg font-semibold text-slate-900">Manual Field Editor</h2>
      </div>
      <div className="p-4 space-y-4 max-h-96 overflow-y-auto">
        {formFields.length === 0 ? (
          <p className="text-slate-500 text-center">No form fields found. Upload a PDF with form fields to get started.</p>
        ) : (
          formFields.map(renderField)
        )}
      </div>
    </div>
  );
});

export default ManualFieldEditor;